<template>
  <div v-if="currentInfo"
       class="scroll">
    <!-- 单位名称 -->
    <van-cell title="单位名称"
              :value="currentInfo.cooperationName" />
    <van-cell title="联系电话"
              :value="currentInfo.phone" />
    <van-cell title="联系人"
              :value="currentInfo.contact" />
    <van-cell style="margin-bottom:20px;"
              title="电子邮箱"
              :value="currentInfo.email" />
    <!-- 折扣优惠 -->
    <van-cell style="margin-top:20px;"
              title="折扣优惠"
              :value="currentInfo.cooperDiscount+'折'" />
    <!-- 挂账额度 -->
    <van-cell style="margin-top:20px;"
              title="挂账额度"
              is-link
              :value="'￥'+currentInfo.accountLimit" />
    <van-cell title="结账周期"
              is-link
              @click="show_cycle = true"
              :value="billCycle" />
    <van-cell title="预存金额"
              is-link
              :value="'￥'+currentInfo.accountLimit" />
    <!-- 总消费额 -->
    <van-cell style="margin-top:20px;"
              title="总消费额"
              :value="'￥'+currentInfo.accountLimit" />
    <van-cell title="长租订单"
              is-link
              value="查看" />
    <van-cell title="预存金额"
              :value="currentInfo.cooperationName" />
    <!-- 房间管理 -->
    <van-cell style="margin-top:20px;"
              title="房间管理"
              @click="show_status = !show_status">
      {{ show_status == true?'启用':'关闭' }}
      <van-icon slot="right-icon"
                size="24px"
                style="padding-left:20px;"
                :color="show_status == true?'#FF7D40':'#000'"
                :name="show_status == true?'checked':'circle'"
                class="custom-icon" />
    </van-cell>
    <van-cell title="房间号"
              is-link
              :value="currentInfo.roomNum ?currentInfo.roomNum+'间':'0间'" />
    <!-- 协议状态 -->
    <van-cell style="margin-top:20px;"
              title="协议状态"
              @click="show_status = !show_status">
      {{ show_status == true?'启用':'关闭' }}
      <van-icon slot="right-icon"
                size="24px"
                style="padding-left:20px;"
                :color="show_status == true?'#FF7D40':'#000'"
                :name="show_status == true?'checked':'circle'"
                class="custom-icon" />
    </van-cell>
    <!-- 员工 -->
    <van-cell style="margin-top:20px;"
              title="员工管理"
              is-link />
    <!-- 选择-物业类型 -->
    <van-popup v-model="show_cycle"
               position="bottom">
      <van-picker show-toolbar
                  :columns="cycleAll"
                  @confirm="confirmCycle"
                  @cancel="show_cycle = false" />
    </van-popup>
  </div>
</template>
<script>
import { GetBusinessCooperation } from '@/api/api_business';
import { GetSystemDictDataByType } from '@/api/api_query';
export default {
    data() {
        return {
            currentInfo: null,
            billCycle: null,
            show_cycle: false,
            show_status: false,
            cycleAll: []
        };
    },
    created() {
        this.$store.commit('setHeader', this.$route.query.cooperationName);
        this.getInfo();
    },
    methods: {
        getInfo() {
            GetBusinessCooperation({ cooperationId: this.$route.query.cooperationId }).done((res) => {
                this.currentInfo = res.data;
                this.getType();
            });
        },
        getType() {
            GetSystemDictDataByType({ dictType: 'business_bill_cycle' }).done((res) => {
                this.cycleOldAll = res.data;
                this.cycleAll = res.data.map((item) => item.dictLabel);
                if (this.currentInfo.billCycle) {
                    this.billCycle = this.cycleOldAll.find((item) => item.dictValue == this.currentInfo.billCycle).dictLabel;
                    this.billCycle_value = this.currentInfo.billCycle;
                }
            });
        },
        confirmCycle(e) {
            this.billCycle = e;
            this.billCycle_value = this.cycleOldAll.find((item) => item.dictLabel == e).dictValue;
            this.show_cycle = false;
        }
    }
};
</script>
<style scoped>
.scroll {
    position: fixed;
    top: 60px;
    left: 0;
    right: 0;
    bottom: 10px;
    overflow: scroll;
}
/deep/ .van-cell__title {
    text-align: left;
    color: #989898;
}
/deep/ .van-cell__value {
    color: #000;
}
</style>



